.navbar .correct {
  -webkit-animation: .4s correct;
          animation: .4s correct;
}

.navbar .incorrect {
  -webkit-animation: .4s incorrect;
          animation: .4s incorrect;
}

@-webkit-keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #A5ADB0;
  }

  100% {
    color: white;
  }
}

@keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #A5ADB0;
  }

  100% {
    color: white;
  }
}

