.navbar {
  background: #ED4262;
  opacity: .85;
  min-height: 50px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  color: #D4AF37;
  font-family: "beynoregular";
  font-size: 2rem;
  z-index: 99;
  white-space: nowrap;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

.navbar li {
  display: inline-block;
  padding: 10px 0px;
  width: 33.3%;
  text-align: center;
  font-family: "Lato", sans-serif;
  font-size: 2rem;
  text-shadow: 2px 1px black;
}

.navbar a {
  text-decoration: none;
  color: white;
  font-weight: 600;
}

@media (max-width: 420px) {
  .navbar li:first-child {
    width: 25%;
  }

  .navbar li:nth-child(2) {
    width: 35%;
  }

  .navbar li:last-child {
    width: 40%;
  }
}

